<template>
  <div>
    <!-- <el-upload action="#" list-type="picture-card" :auto-upload="false">
      <i slot="default" class="el-icon-plus"></i>
      <div slot="file" slot-scope="{file}">
        <img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
        <span class="el-upload-list__item-actions">
          <span class="el-upload-list__item-preview" @click="handlePictureCardPreview(file)">
            <i class="el-icon-zoom-in"></i>
          </span>
          <span v-if="!disabled" class="el-upload-list__item-delete" @click="handleDownload(file)">
            <i class="el-icon-download"></i>
          </span>
          <span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
            <i class="el-icon-delete"></i>
          </span>
        </span>
      </div>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog> -->
    <el-upload 
      ref="uploadBox"
      action="233" :limit="1" :auto-upload="false" 
      :http-request="postPhoto" list-type="picture-card"
      :on-preview="handlePictureCardPreview" 
      :on-remove="handleRemove" 
      :on-change="fail">
      <i class="el-icon-plus"></i>
    </el-upload>
    <span>只支持jpg与png格式文件上传</span>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt>
    </el-dialog>
    <el-button @click="postImg" type="success">上传</el-button>

    <div>
      <p>上传成功列表：</p>
      <div class="imgBox">
        <ul>
          <li v-for="(item, index) in allPath" :key="item.imgPath+index">
            <img :src="item.imgPath" />
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  export default {
    computed:{
      ...mapState(['userInfo'])
    },
    data() {
      return {
        allPath: [],
        dialogImageUrl: '',
        dialogVisible: false,
        disabled: false
      };
    },
    mounted(){
      this.getMyImg()
    },
    methods: {
      async getMyImg(){
        let rel = await this.postData('/getMyImg',{userId: this.userInfo.id});
        console.log('rel-img:',rel);
        if(rel){
          this.allPath=rel.data.data
        }
      },
      postPhoto(data){
        console.log('postPhoto:',data);
      },
      fail(file) {
        console.log('file:',file);
        this.dialogImageUrl = file.url;
        this.dialogImageUrl = new window.File([this.dialogImageUrl], file.name, {
          type: file.type
        });
        // 上面负责回显，调用的本地
        // this.dialogVisible = true;
        // 这里我在data中初始化了一个变量，imgPost，给他赋值。
        this.imgPost = file.raw
      },
      postImg() {
        // let token1 = sessionStorage.getItem("token");
        // 由于我们的后台需要发送token，所以这里存了一下\

        // 首先，创建一个新的FormData。
        let formData1 = new FormData();

        formData1.append("file", this.imgPost)
        formData1.append("userId", this.userInfo.id)
        formData1.append("username", this.userInfo.username)
        // formData1.append("token", token1)
        // 划重点！！这里需要特别说明，你会发现发现打印这个formData1为空。
        // 这里的append并不是没有追加进去，是因为控制台打印的formdata是原型，为类的私有字段，外界访问不到，可以在发送的请求中查看

        // 这就是要传的文件
        console.log(formData1)
        // upload是我的封装的接口 ===== 换成你自己的即可
        // 如果一切顺利，那么这里的res应该就有你们后台给的回调url啦。
        this.$axios.post(
          `${window.baseUrl}/fileUpload`,
          formData1,
          ).then((res) =>{          //这里使用了ES6的语法
            console.log('response:',res,process.env)       //请求成功返回的数据
            if(res.data){
              // let imgPath = window.crossPath+res.data
              this.allPath=res.data.data
              this.$refs.uploadBox.clearFiles();
              this.myMsg('上传成功!','success')
            }
          }).catch((error) =>{
            this.myMsg('上传失败!','error')
            console.log(error)       //请求失败返回的数据
           
          })

      },
      handleRemove(file) {
          console.log(file);
        },
          handlePictureCardPreview(file) {
          this.dialogImageUrl = file.url;
          this.dialogVisible = true;
        },
          handleDownload(file) {
          console.log(file);
        }
    }
    }
</script>

<style>
  .imgBox{
    border: 1px solid #ccc;
    height: 500px;
    overflow: auto;
  }
  .imgBox li{
    height: 300px;
    width: 300px;
    border: 1px solid #eee;
    padding: 5px;
    margin: 10px;
    float: left;
  }

  .imgBox li img{
    width: 100%;
    height: 100%;
  }
</style>