<template>
  <div style="height: 100%; display: flex; flex-direction: column;">
    <p class="h50">权限列表：(基础权限由后台返回)
      <el-button type="primary" style="margin-left: 50px;" @click="addModule">增加模块</el-button>
      <el-button type="success" @click="backModule">恢复出厂模块</el-button>
    </p>
    <!-- <rightBox :allRight="allRight" style="flex: 1; overflow: auto; margin-bottom: 50px;"></rightBox> -->

    <div style="height: cal(100%-50px); overflow: auto;">
      <p class="pd-l10" v-if="theAllRight.length==0">尚未有权限数据，<span class="myBlue hand" @click="addModule">马上添加</span></p>
      <rightBox 
        v-else
        :allRight="theAllRight"
        :showCheckbox="showCheckbox"
        @pageSelect="pageSelectFun"
        @delOnePage="delOnePage"
        >
      </rightBox>
    </div>

    <el-dialog
      :title="boxName"
      :visible.sync="showModule"
      width="40%">
      <el-form ref="form" :model="oneModule" label-width="80px">
        <el-form-item label="模块名称">
          <el-input v-model="oneModule.name"></el-input>
        </el-form-item>
        <el-form-item label="图标">
          <el-select v-model="oneModule.icon" placeholder="请选择图标">
            <el-option label="饿了么" value="el-icon-eleme">
              <i class="el-icon-eleme"></i>
            </el-option>
            <el-option label="加号" value="el-icon-plus">
              <i class="el-icon-plus"></i>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="子模块">
          <p><el-button type="success" @click="addSonModule(oneModule)"><i class="el-icon-plus"></i>添加</el-button></p>
          <ul style="font-size: 14px;">
            <li v-for="(item,index) in oneModule.children">
              {{item.name}}: {{item.path}} <i class="el-icon-remove hand" @click="removeSonModule(item)"></i>
            </li>
          </ul>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showModule = false">取 消</el-button>
        <el-button type="primary" @click="sureAdd(oneModule)">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      :title="sonBoxName"
      :visible.sync="showSonBox"
      width="30%">
      <el-form ref="sonform" :model="sonModule" label-width="80px">
        <el-form-item label="模块名称">
          <el-input v-model="sonModule.name"></el-input>
        </el-form-item>
        <el-form-item label="路径">
          <el-input v-model="sonModule.path"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showSonBox = false">取 消</el-button>
        <el-button type="primary" @click="sureAddSon(sonModule)">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import rightBox from './rightBox'
  export default {
    components:{
      rightBox
    },
    data(){
      return{
        boxName: '添加模块',
        sonBoxName: '添加子模块',
        showSonBox: false,
        showModule: false,
        showCheckbox: false,
        allPerson: [],
        theAllRight: [],
        oneModule: {
          id: 1,
          name: '模块名称',
          icon: 'el-icon-user-solid',
          checked: true,
          children:[]
        },
        sonModule:{
          name: '子模块名称',
          path: '/user',
          checked: true,
          children:[
            {
                name: '增',
                path: 'add',
                checked: true,
              },
              {
                name: '删',
                path: 'delete',
                checked: true
              },{
                name: '改',
                path: 'update',
                checked: true
              },{
                name: '查',
                path: 'read',
                checked: true
              }
            ]
        },
        allRight:[
          {
              id: 1,
              name: '用户管理',
              icon: 'el-icon-user-solid',
              checked: true,
              children:[
                {
                  id: 11,
                  name: '用户列表',
                  path: '/user',
                  checked: true,
                  children:[
                  {
                      id: 111,
                      name: '增',
                      path: 'add',
                      checked: true,
                    },
                    {
                      id: 112,
                      name: '删',
                      path: 'delete',
                      checked: true
                    },{
                      id: 113,
                      name: '改',
                      path: 'update',
                      checked: true
                    },{
                      id: 114,
                      name: '查',
                      path: 'read',
                      checked: true
                    }
                  ]
                }
              ]
          },
          {
            id: 2,
            name: '列车管理',
            icon: 'el-icon-s-cooperation',
            checked: true,
            children:[
              {
                id: 21,
                name: '列车列表',
                checked: true,
                path: '/carManager',
                children:[
                {
                    id: 211,
                    name: '增',
                    path: 'add',
                    checked: true,
                  },
                  {
                    id: 212,
                    name: '删',
                    path: 'delete',
                    checked: true
                  },{
                    id: 213,
                    name: '改',
                    path: 'update',
                    checked: true
                  },{
                    id: 214,
                    name: '查',
                    path: 'read',
                    checked: true
                  }
                ]
              },
              {
                id: 22,
                name: '数据导入',
                path: '/importExcel',
                checked: true,
                children:[
                {
                    id: 221,
                    name: '增',
                    path: 'add',
                    checked: true
                  },
                  {
                    id: 222,
                    name: '删',
                    path: 'delete',
                    checked: true
                  },{
                    id: 223,
                    name: '改',
                    path: 'update',
                    checked: true
                  },{
                    id: 224,
                    name: '查',
                    path: 'read',
                    checked: true
                  }
                ]
              }
            ]
          },
          {
            id: 3,
            name: '权限管理',
            icon: 'el-icon-s-tools',
            checked: true,
            children:[
              {
                id: 31,
                name: '角色列表',
                path: '/role',
                checked: true,
                children:[
                {
                    id: 311,
                    name: '增',
                    path: 'add',
                    checked: true,
                  },
                  {
                    id: 312,
                    name: '删',
                    path: 'delete',
                    checked: true
                  },{
                    id: 313,
                    name: '改',
                    path: 'update',
                    checked: true
                  },{
                    id: 314,
                    name: '查',
                    path: 'read',
                    checked: true
                  }
                ]
              },
              {
                id: 32,
                name: '权限列表',
                path: '/right',
                checked: true,
                children:[
                {
                    id: 321,
                    name: '增',
                    path: 'add',
                    checked: true
                  },
                  {
                    id: 322,
                    name: '删',
                    path: 'delete',
                    checked: true
                  },{
                    id: 323,
                    name: '改',
                    path: 'update',
                    checked: true
                  },{
                    id: 324,
                    name: '查',
                    path: 'read',
                    checked: true
                  }
                ]
              }
            ]
          }
        ]
      }
    },
    mounted(){
      // this.theAllRight = JSON.parse(JSON.stringify(this.allRight))
      // let that = this;
      this.getRight()
    },
    methods:{
      backModule(){
        this.composeData(this.allRight)
      },
      delOnePage(item){
        let arr = JSON.parse(JSON.stringify(this.theAllRight));
        for(let i=0;i<arr.length;i++){
          if(arr[i].name == item.name){
            arr.splice(i,1);
            break;
          }
        }
        this.composeData(arr)
      },
      async getRight(){
        let rel = await this.getData('/getRight');
        let data = rel.data.data
        console.log('getRight-rel:',rel,data);
        this.theAllRight = JSON.parse(data[0].content);
      },
      sureAddSon(sonModule){
        console.log('sonModule:',sonModule);
        this.oneModule.children.push(JSON.parse(JSON.stringify(sonModule)))
        this.showSonBox = false;
      },
      removeSonModule(item){
        console.log('removeSonModule',item);
        let arr = this.oneModule.children
        for(let i=0;i<arr.length;i++){
          if(arr[i].path == item.path){
            arr.splice(i,1);
            break;
          }
        }
      },
      sureAdd(oneModule){
        console.log('sureAdd',oneModule);
        let allR = JSON.parse(JSON.stringify(this.theAllRight));
        allR.push(oneModule)
        this.showModule = false;
        this.composeData(allR)
      },
      async composeData(allR){
        let data0 = {
          content: JSON.stringify(allR)
        }
        let rel = await this.postData('/saveRight',data0)
        let data = rel.data.data
        console.log('getRight-rel:',rel,data);
        this.theAllRight = JSON.parse(data[0].content);
      },
      addModule(){
        console.log('addModule');
        this.showModule = true;
      },
      addSonModule(module){
        console.log('addSonModule:',module);
        this.showSonBox = true;
      },
      pageSelectFun(page){
         console.log('pageSelect:',page);
      },
      sonPageSelectFun(page){
         // console.log('sonPageSelect:',page);
      },
      sunPageSelectFun(page){
         // console.log('sunPageSelect:',page);
      }
    }
  };
  </script>

  